export default "import React from 'react';\r\nimport {findDOMNode} from 'react-dom';\r\nimport FormControl from '@material-ui/core/FormControl';\r\nimport FormControlLabel from '@material-ui/core/FormControlLabel';\r\nimport FormLabel from '@material-ui/core/FormLabel';\r\nimport Radio from '@material-ui/core/Radio';\r\nimport RadioGroup from '@material-ui/core/RadioGroup';\r\nimport Grid from '@material-ui/core/Grid';\r\nimport Button from '@material-ui/core/Button';\r\nimport Popover from '@material-ui/core/Popover';\r\nimport Input from '@material-ui/core/Input';\r\nimport InputLabel from '@material-ui/core/InputLabel';\r\nimport Box from '@material-ui/core/Box';\r\n\r\nclass AnchorPlayground extends React.Component {\r\n  state = {\r\n    open: false,\r\n    anchorEl: null,\r\n    anchorOriginVertical: 'bottom',\r\n    anchorOriginHorizontal: 'center',\r\n    transformOriginVertical: 'top',\r\n    transformOriginHorizontal: 'center',\r\n    positionTop: 200, // Just so the popover can be spotted more easily\r\n    positionLeft: 400, // Same as above\r\n    anchorReference: 'anchorEl',\r\n  };\r\n  button = null;\r\n\r\n  handleChange = (key) => (event, value) => {\r\n    this.setState({\r\n      [key]: value,\r\n    });\r\n  };\r\n\r\n  handleNumberInputChange = (key) => (event) => {\r\n    this.setState({\r\n      [key]: parseInt(event.target.value, 10),\r\n    });\r\n  };\r\n\r\n  handleClickButton = () => {\r\n    this.setState({\r\n      open: true,\r\n      anchorEl: findDOMNode(this.button),\r\n    });\r\n  };\r\n\r\n  handleRequestClose = () => {\r\n    this.setState({\r\n      open: false,\r\n    });\r\n  };\r\n\r\n  render() {\r\n    const {\r\n      open,\r\n      anchorEl,\r\n      anchorOriginVertical,\r\n      anchorOriginHorizontal,\r\n      transformOriginVertical,\r\n      transformOriginHorizontal,\r\n      positionTop,\r\n      positionLeft,\r\n      anchorReference,\r\n    } = this.state;\r\n\r\n    return (\r\n      <div>\r\n        <Box mb={5} clone>\r\n          <Button\r\n            ref={(node) => {\r\n              this.button = node;\r\n            }}\r\n            variant='contained'\r\n            onClick={this.handleClickButton}>\r\n            Open Popover\r\n          </Button>\r\n        </Box>\r\n        <Popover\r\n          open={open}\r\n          anchorEl={anchorEl}\r\n          anchorReference={anchorReference}\r\n          anchorPosition={{top: positionTop, left: positionLeft}}\r\n          onClose={this.handleRequestClose}\r\n          anchorOrigin={{\r\n            vertical: anchorOriginVertical,\r\n            horizontal: anchorOriginHorizontal,\r\n          }}\r\n          transformOrigin={{\r\n            vertical: transformOriginVertical,\r\n            horizontal: transformOriginHorizontal,\r\n          }}>\r\n          <Box component='p' m={3}>\r\n            The content of the Popover.\r\n          </Box>\r\n        </Popover>\r\n        <Grid container>\r\n          <Grid item xs={12} sm={6}>\r\n            <FormControl component='fieldset'>\r\n              <FormLabel component='legend'>Anchor Reference</FormLabel>\r\n              <RadioGroup\r\n                row\r\n                aria-label='anchorReference'\r\n                name='anchorReference'\r\n                value={this.state.anchorReference}\r\n                onChange={this.handleChange('anchorReference')}>\r\n                <FormControlLabel\r\n                  value='anchorEl'\r\n                  control={<Radio />}\r\n                  label='Anchor El'\r\n                />\r\n                <FormControlLabel\r\n                  value='anchorPosition'\r\n                  control={<Radio />}\r\n                  label='Anchor Position'\r\n                />\r\n              </RadioGroup>\r\n            </FormControl>\r\n          </Grid>\r\n          <Grid item xs={12} sm={6}>\r\n            <FormControl>\r\n              <InputLabel htmlFor='position-top'>\r\n                Anchor Position Top\r\n              </InputLabel>\r\n              <Input\r\n                id='position-top'\r\n                type='number'\r\n                value={this.state.positionTop}\r\n                onChange={this.handleNumberInputChange('positionTop')}\r\n              />\r\n            </FormControl>\r\n            &nbsp;\r\n            <FormControl>\r\n              <InputLabel htmlFor='position-left'>\r\n                Anchor Position Left\r\n              </InputLabel>\r\n              <Input\r\n                id='position-left'\r\n                type='number'\r\n                value={this.state.positionLeft}\r\n                onChange={this.handleNumberInputChange('positionLeft')}\r\n              />\r\n            </FormControl>\r\n          </Grid>\r\n          <Grid item xs={12} sm={6}>\r\n            <FormControl component='fieldset'>\r\n              <FormLabel component='legend'>Anchor Origin Vertical</FormLabel>\r\n              <RadioGroup\r\n                aria-label='anchorOriginVertical'\r\n                name='anchorOriginVertical'\r\n                value={this.state.anchorOriginVertical}\r\n                onChange={this.handleChange('anchorOriginVertical')}>\r\n                <FormControlLabel value='top' control={<Radio />} label='Top' />\r\n                <FormControlLabel\r\n                  value='center'\r\n                  control={<Radio />}\r\n                  label='Center'\r\n                />\r\n                <FormControlLabel\r\n                  value='bottom'\r\n                  control={<Radio />}\r\n                  label='Bottom'\r\n                />\r\n              </RadioGroup>\r\n            </FormControl>\r\n          </Grid>\r\n          <Grid item xs={12} sm={6}>\r\n            <FormControl component='fieldset'>\r\n              <FormLabel component='legend'>\r\n                Transform Origin Vertical\r\n              </FormLabel>\r\n              <RadioGroup\r\n                aria-label='transformOriginVertical'\r\n                name='transformOriginVertical'\r\n                value={this.state.transformOriginVertical}\r\n                onChange={this.handleChange('transformOriginVertical')}>\r\n                <FormControlLabel value='top' control={<Radio />} label='Top' />\r\n                <FormControlLabel\r\n                  value='center'\r\n                  control={<Radio />}\r\n                  label='Center'\r\n                />\r\n                <FormControlLabel\r\n                  value='bottom'\r\n                  control={<Radio />}\r\n                  label='Bottom'\r\n                />\r\n              </RadioGroup>\r\n            </FormControl>\r\n          </Grid>\r\n          <Grid item xs={12} sm={6}>\r\n            <FormControl component='fieldset'>\r\n              <FormLabel component='legend'>Anchor Origin Horizontal</FormLabel>\r\n              <RadioGroup\r\n                row\r\n                aria-label='anchorOriginHorizontal'\r\n                name='anchorOriginHorizontal'\r\n                value={this.state.anchorOriginHorizontal}\r\n                onChange={this.handleChange('anchorOriginHorizontal')}>\r\n                <FormControlLabel\r\n                  value='left'\r\n                  control={<Radio />}\r\n                  label='Left'\r\n                />\r\n                <FormControlLabel\r\n                  value='center'\r\n                  control={<Radio />}\r\n                  label='Center'\r\n                />\r\n                <FormControlLabel\r\n                  value='right'\r\n                  control={<Radio />}\r\n                  label='Right'\r\n                />\r\n              </RadioGroup>\r\n            </FormControl>\r\n          </Grid>\r\n          <Grid item xs={12} sm={6}>\r\n            <FormControl component='fieldset'>\r\n              <FormLabel component='legend'>\r\n                Transform Origin Horizontal\r\n              </FormLabel>\r\n              <RadioGroup\r\n                row\r\n                aria-label='transformOriginHorizontal'\r\n                name='transformOriginHorizontal'\r\n                value={this.state.transformOriginHorizontal}\r\n                onChange={this.handleChange('transformOriginHorizontal')}>\r\n                <FormControlLabel\r\n                  value='left'\r\n                  control={<Radio />}\r\n                  label='Left'\r\n                />\r\n                <FormControlLabel\r\n                  value='center'\r\n                  control={<Radio />}\r\n                  label='Center'\r\n                />\r\n                <FormControlLabel\r\n                  value='right'\r\n                  control={<Radio />}\r\n                  label='Right'\r\n                />\r\n              </RadioGroup>\r\n            </FormControl>\r\n          </Grid>\r\n        </Grid>\r\n      </div>\r\n    );\r\n  }\r\n}\r\n\r\nexport default AnchorPlayground;\r\n";